// src/pages/dashboard/index.tsx
import Head from "next/head";
import { useRouter } from "next/router";
import DashboardLayout from "@/components/DashboardLayout";
import PackagesSection from "@/components/dashboard/PackagesSection";
import HistorySection from "@/components/dashboard/HistorySection";
import ReservarPaquete from "@/components/dashboard/ReservarPaquete";

export default function DashboardPage() {
  const router = useRouter();
  const { view, type, sessions, priceId } = router.query;
  const isReservar =
    view === "reservar-paquete" &&
    typeof type === "string" &&
    typeof sessions === "string" &&
    typeof priceId === "string";

  return (
    <>
      <Head>
        <title>Dashboard • Bloom Fisio</title>
      </Head>
      <DashboardLayout>
        <div className="dashboard-container">
          {isReservar ? (
            <ReservarPaquete
              type={type}
              sessions={Number(sessions)}
              priceId={priceId}
            />
          ) : (
            <>
              <PackagesSection />
              <HistorySection />
            </>
          )}
        </div>
      </DashboardLayout>
    </>
  );
}