// src/components/dashboard/ReservarPaquete.tsx
"use client";

import React, { useState, useEffect } from "react";
import { useRouter } from "next/router";
import { useSession } from "next-auth/react";
import Calendar from "react-calendar";
import "react-calendar/dist/Calendar.css";
import {
  Container,
  Button,
  Alert,
  ListGroup,
  Spinner,
} from "react-bootstrap";
import { loadStripe } from "@stripe/stripe-js";

interface Props {
  type: string;
  sessions: number;
  priceId: string;
}

export default function ReservarPaquete({ type, sessions, priceId }: Props) {
  const router = useRouter();
  const { data: session, status } = useSession();
  const [slots, setSlots] = useState<{ date: Date | null; hora: number | null }[]>(
    Array.from({ length: sessions }, () => ({ date: null, hora: null }))
  );
  const [current, setCurrent] = useState(0);
  const [error, setError] = useState("");

  const now = new Date();
  const maxDate = new Date(now.getTime() + 30 * 24 * 60 * 60 * 1000);

  useEffect(() => {
    if (status === "unauthenticated") router.replace("/login");
  }, [status, router]);

  const thisSlot = slots[current];

  const onDay = (d: Date) => {
    if (d < now || d > maxDate) return;
    setSlots((s) => {
      const nxt = [...s];
      nxt[current] = { date: d, hora: null };
      return nxt;
    });
  };
  const onHour = (h: number) => {
    setSlots((s) => {
      const nxt = [...s];
      nxt[current] = { ...nxt[current], hora: h };
      return nxt;
    });
  };

  const doneCount = slots.filter((s) => s.date && s.hora !== null).length;

  const next = () => {
    if (!thisSlot.date || thisSlot.hora === null) {
      setError("Elige fecha y hora antes de continuar");
      return;
    }
    setError("");
    setCurrent((c) => Math.min(c + 1, sessions - 1));
  };

  const confirm = async () => {
    const dates = slots.map((s) => s.date!.toISOString());
    const hours = slots.map((s) => `${s.hora}:00`);
    const body = {
      userId: session?.user?.id,
      lineItems: [{ price: priceId, quantity: 1 }],
      metadata: {
        servicio: type,
        dates: JSON.stringify(dates),
        hours: JSON.stringify(hours),
        sessionsCount: sessions.toString(),
      },
    };
    const res = await fetch("/api/stripe/checkout", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(body),
    });
    const { sessionId } = await res.json();
    const stripe = await loadStripe(process.env.NEXT_PUBLIC_STRIPE_PUBLISHABLE_KEY!);
    stripe?.redirectToCheckout({ sessionId });
  };

  if (status === "loading" || !session) {
    return <Spinner className="m-5" animation="border" />;
  }

  return (
    <Container className="py-5">
      <h2 className="dashboard-header">
        {doneCount < sessions
          ? `Sesión ${current + 1} de ${sessions}`
          : "Resumen"}
      </h2>
      {error && <Alert variant="danger">{error}</Alert>}

      {doneCount < sessions ? (
        <>
          <Calendar
            onClickDay={onDay}
            value={thisSlot.date || now}
            minDate={now}
            maxDate={maxDate}
          />
          {thisSlot.date && (
            <div className="mt-3">
              <strong>Hora:</strong>{" "}
              {[10,11,12,13,14,15,16,17,18].map((h) => (
                <Button
                  key={h}
                  variant={thisSlot.hora === h ? "primary" : "outline-primary"}
                  className="me-1 mb-2 slot-btn"
                  onClick={() => onHour(h)}
                >
                  {h}:00
                </Button>
              ))}
            </div>
          )}
          <Button className="mt-4 btn-orange" onClick={next}>
            {current < sessions - 1 ? "Siguiente" : "Finalizar"}
          </Button>
        </>
      ) : (
        <>
          <ListGroup className="mb-4">
            {slots.map((s, i) => (
              <ListGroup.Item key={i}>
                Sesión {i + 1}: {new Date(s.date!).toLocaleDateString()} a las{" "}
                {s.hora}:00
              </ListGroup.Item>
            ))}
          </ListGroup>
          <Button className="btn-orange" onClick={confirm}>
            Confirmar y pagar
          </Button>
        </>
      )}
    </Container>
  );
}