// src/components/dashboard/HistorySection.tsx
"use client";

import React, { useEffect, useState } from "react";
import { Table, Spinner, Alert } from "react-bootstrap";
import { Edit3 } from "react-feather";

interface RowData {
  id: string;
  service: string;
  therapist: string;
  datetime: string;
  edited: boolean;
}

export default function HistorySection() {
  const [rows, setRows] = useState<RowData[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetch("/api/appointments")
      .then((r) => r.json())
      .then((d) => setRows(d.reservations))
      .catch(console.error)
      .finally(() => setLoading(false));
  }, []);

  if (loading) return <Spinner className="m-5" animation="border" />;
  if (rows.length === 0)
    return (
      <Alert variant="info" className="text-center">
        No hay sesiones agendadas.
      </Alert>
    );

  return (
    <div className="overflow-auto">
      <Table hover responsive className="dashboard-table">
        <thead>
          <tr>
            <th>Servicio</th>
            <th>Terapeuta</th>
            <th>Fecha y hora</th>
            <th>Acciones</th>
          </tr>
        </thead>
        <tbody>
          {rows.map((r) => {
            const dt = new Date(r.datetime);
            return (
              <tr key={r.id} className={r.edited ? "table-warning" : ""}>
                <td>{r.service}</td>
                <td>{r.therapist}</td>
                <td>
                  {dt.toLocaleDateString()} •{" "}
                  {dt.toLocaleTimeString([], {
                    hour: "2-digit",
                    minute: "2-digit",
                  })}
                </td>
                <td>
                  <span
                    title={
                      r.edited ? "Ya editaste esta sesión" : "Editar sesión"
                    }
                  >
                    <Edit3
                      size={18}
                      className={r.edited ? "icon-disabled" : "icon-editable"}
                      onClick={() => {
                        if (!r.edited) {
                          /* abrir modal de edición */
                        }
                      }}
                    />
                  </span>
                </td>
              </tr>
            );
          })}
        </tbody>
      </Table>
    </div>
  );
}